body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-image: #EFF3F7;  
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, #root{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.app--loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Movie {
  background-color: white;
  width: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: 50px;
  text-overflow: ellipsis;
  padding: 0 20px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
  }

  .Movie__Column{
    width:30%;
    box-sizing:border-box;
    text-overflow: ellipsis;
  }

  .Movie__Column:last-child{
    padding: 20px 0;
    width: 60%;
  }

  .Movie h1{
    font-size:20px;
    font-weight: 600;
  }

  .Movie__Genres .Movie__Genre {
    margin-right: 10px;
    color: #B4B5BD;
  }

  .Movie .Movie__Synopsis {
    text-overflow: ellipsis;
    color: #B4B5BD;
    overflow: hidden;
  }

  .Movie .Movie__Poster {
    max-width: 100%;
    position: relative;
    top: -20px;
    box-shadow: -10x 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80. 0.22);
  }

  @media screen and (min-width:320px) and (max-width:667px) {
    .Movie {
      width:100%;
    }
  }

  @media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait) {
    .Movie{
      width: 100%;
      -webkit-flex-direction: column;
              flex-direction: column;
    }

    .Movie__Poster{
      top:0;
      left:0;
      width:100%;
    }
    .Movie__Column{
      widows: 100% !important;
    }
  }

